.about {
    background-repeat: no-repeat;
    background-size: cover;

    &__content {
        .glass_text {
            width: 100%;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
            overflow: hidden;
        }
    }

    &__picture img {
        width: 100%;
        height: 400px;
        object-fit: cover;
        border-radius: 8px;
    }

    &__numbers {
        margin: 2rem 0 1rem -0.5rem;
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        &-item {
            margin: .5rem;
            width: 150px;
            padding: 16px;
            border-radius: 8px;
            text-align: center;

            h2 {
                font-weight: 700;
                font-size: 22px;
            }

            p {
                font-weight: 400;
            }
        }
    }

    &__img {
        border-radius: 8px;
        width: 100%;
        height: 220px !important;
        object-fit: cover;
    }
}