.media__card {
    box-shadow   : 0px 10px 25px 0px #00000014;
    border-radius: 12px;

    &-img {
        width        : 100%;
        height       : 100%;
        object-fit   : cover;
        border-radius: 12px;
    }

    .card__title {
        width             : 100%;
        display           : -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow          : hidden;
    }
}